@use 'variables' as v;
@use 'mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;    
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.8rem;
}
a {
    text-decoration: none;
}
img {
    width: 100%;
    height: auto;
}

.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

.swal2-popup{
    font-size: 1.5rem!important;
}

.img-small{
    width: 10rem;
    margin-top: 2rem;
}